import React, { Component } from 'react';
import {
  Switch,
  Route
} from 'react-router-dom'
import { CookiesProvider } from 'react-cookie';
import App from './App';

class TopApp extends Component {
  render() {
    return (
      <CookiesProvider>
        <div className="TopApp">
          <Switch>
            <Route path="/" exact component={App}/>
            <Route component={App} />
          </Switch>
        </div>
      </CookiesProvider>
    );
  }
}

export default TopApp;
